import * as React from "react";
import { graphql } from "gatsby";

import Layout from "../components/layout";

export const query = graphql`
  query BlogPageQuery($id: String!) {
    mdx(id: { eq: $id }) {
      id
      body
      tableOfContents(maxDepth: 2)
      slug
      frontmatter {
        title
        date
        description
      }
    }
  }
`;

const Page = ({ data }) => {
  const post = data.mdx;
  // 左侧目录
  const sidebarData = JSON.parse(
    window.localStorage.getItem(`sidebar-${post.slug.split("/")[0]}`)
  );

  // 右侧目录
  const catalogue = post.tableOfContents.items || [];

  const seo = {
    title: post.frontmatter.title,
    description: post.frontmatter.description,
  };

  return (
    <Layout
      sidebarData={sidebarData}
      contentData={post}
      catalogue={catalogue}
      seo={seo}
    ></Layout>
  );
};

export default Page;
